.products {
    background-color: #fff;
    padding: 30px 28px;
    position: fixed;
    width: 90%;
    top: 20px;
    left: 0;
    transform: translateX(-140%);
    border-radius: 20px;
    transition: all 500ms ease;
    transition-delay: 300ms;
    opacity: 0;
}
.head_products {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #6C6C6C;
    margin-bottom: 1.5rem;
}
.head_products h3 {
    font-size: 14px;
}
.close_btn {
    border: none;
    background-color: transparent;
    cursor: pointer;
    pointer-events: none;
}
.products_list li {
    display: flex;
    align-items: flex-start;
    margin: 20px 0;
}
.products_list strong {
    color: #00152F;
    font-size: 14px;
}
.products_list div {
    margin-left: 10px;
    width: 100%;
}
.products_list svg {
    width: fit-content;
}
.products_list p {
    color: #6C6C6C;
    font-size: 13px;
    line-height: 1.55;
}
.products-00:hover .products{
    left: 50%;
    transform: translateX(-50%);
    opacity: 1;
}
.products-00:hover .close_lay {
    left: 0;
    transform: translateX(-140%);
}
@media screen and (min-width: 750px) {
    .head_products {
        display: none;
    }
    .products-00 {
        position: relative;
    }
    .products {
        position: absolute;
        left: 0px;
        top: 50px;
        width: 600px;
        transform: translateX(-80%);
        opacity: 0;
        pointer-events: none;
    }
    .products::after {
        content: '';
        position: absolute;
        top: -28px;
        left: 115px;
        border: solid 15px white;
        border-left-color: transparent;
        border-right-color: transparent;
        border-top-color: transparent;
    }
    .products .products_list {
        width: 100%;
        display: block;
    }
    .products-00:hover .products {
        left: 0px;
        transform: translateX(-100px);
        opacity: 1;
        pointer-events: visible;
    }
}