.register {
    margin-top: 20px !important;
}
.register button {
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: all 300ms ease;
}
.register button:first-child {
    margin-right: 12px;
    border: 1px solid transparent;
    background-color: transparent;
}
.register button:first-child:hover {
    border-color: #F6B300;
}
.try_for_free {
    background-color: #133416;
    border: none;
    transition: all 300ms ease;
}
.try_for_free:hover {
    background-color: #F6B300;
}
.try_for_free a {
    color: #fff;
}
@media screen and (min-width: 750px) {
    .register {
        justify-self: flex-end;
        display: flex;
        margin: 0 !important;
        width: auto;
    }
}