.rating {
    display: flex;
    align-items: center;
    margin: 20px 0;
}
.rating svg {
    width: 14px;
    height: 14px;
    margin: 0 3px;
}
.rating .review_company {
    width: 35%;
}
.rating p {
    width: 70%;
    font-size: 9px;
    color: #6C6C6C;
}
@media screen and (min-width: 300px) {
    .rating >p {
        font-size: 10px;
    }
    .rating svg {
        width: 15px;
        height: 15px;
    }
}
@media screen and (min-width: 350px) {
    .rating p {
        width: auto;
        font-size: 14px;
    }
    .rating .review_company {
        width: auto;
        margin-left: 20px;
    }
}
@media screen and (min-width: 750px) {
    .rating p {
        font-size: 12px;
    }
    .rating svg {
        width: 20px;
        height: 20px;
        margin: 0 10px;
    }
    .rating .review_company {
        margin-left: 12px;
    }
}
@media screen and (min-width: 850px) {
    .rating p {
        font-size: 15px;
    }
}