header {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
}
@media screen and (min-width: 750px) {
    .logo {
        width: 20%;
    }
    header a {
        font-size: 14px;
    }
}
@media screen and (min-width: 1000px) {
    .container {
        max-width: 900px;
        margin: auto;
    }
}