@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}
body {
  background-color: #F3EDE5;
  max-width: 100%;
}

@media screen and (max-width: 280px) {
    body {
        min-width: 280px;
    }
}