a {
    color: #6C6C6C;
    transition: all 300ms ease;
}
.navigation {
    position: absolute;
    width: 90%;
    background-color: white;
    left: 0%;
    top: 80px;
    transform: translateX(-100%);
    padding: 20px;
    border-radius: 20px;
    transition: all 300ms ease;
}
.navigation li {
    margin: 12px 0;
    width: fit-content;
}
.navigation li:hover a {
    color: #F6B300;
    transform: scale(1.05);
}
.open {
    left: 50%;
    transform: translateX(-50%);
}
@media screen and (max-width: 280px) {
    .navigation {
        min-width: 280px;
    }
    .open {
        left: 0;
        transform: translateX(0);
    }
}
@media screen and (min-width: 750px) {
    .navigation {
        position: static;
        left: auto;
        top: auto;
        transform: translateX(0);
        background-color: transparent;
        width: 80%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        justify-self: flex-end;
        padding: 0;
    }
    .navigation ul {
        width: 60%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .navigation li {
        margin-left: 12px;
    }
}