.section_1 {
    padding: 20px;
}
.section_1 button {
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: all 300ms ease;
}
.intro h1 {
    line-height: 1.45;
}
.intro strong {
    color: #F6B300;
}
.intro >p {
    color: #6C6C6C;
    font-size: 10px;
    line-height: 1.65;
    margin: 5px 0 10px 0;
}
.hero img {
    width: 100%;
    display: none;
}
@media screen and (min-width: 300px) {
    .intro >p {
        font-size: 12px;
    }
}
@media screen and (min-width: 350px) {
    .intro >p {
        font-size: 13px;
    }
}
@media screen and (min-width: 750px) {
    .section_1 {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .intro h1 {
        font-size: 30px;
    }
    .hero img {
        display: block;
    }
    .intro {
        width: 400px;
    }
    .hero {
        width: 270px;
    }
}
@media screen and (min-width: 810px) {
    .intro >p {
        font-size: 17px;
    }
    .intro {
        width: 450px;
    }
    .hero {
        width: 300px;
    }
}
@media screen and (min-width: 900px) {
    .intro {
        width: 500px;
    }
    .intro >p {
        font-size: 18px;
    }
}