.section_2 {
    padding: 20px;
}
.section_2 p {
    text-align: center;
    color: #6C6C6C;
    font-size: 10px;
    margin-bottom: 20px;
}
.companies {
    display: grid;
    grid-template-columns: repeat(2, auto);
    justify-content: space-between;
    margin: 20px 0;
}
.companies svg {
    height: 90px;
    width: 110px;
    margin: -25px 0;
}
@media screen and (min-width: 290px) {
    .companies svg {
        width: 110px;
        margin: -26px 0;
    }
}
@media screen and (min-width: 310px) {
    .companies svg {
        width: 125px;
        margin: -24px 0;
    }
    .section_2 p {
        font-size: 11px;
    }
}
@media screen and (min-width: 320px) {
    .companies {
        width: 270px;
        margin: auto;
    }
}
@media screen and (min-width: 500px) {
    .section_2 p {
        font-size: 16px;
    }
}
@media screen and (min-width: 570px) {
    .companies {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }
}
@media screen and (min-width: 750px) {
    .companies {
        display: flex;
        justify-content: space-between;
        width: 80%;
    }
}
@media screen and (min-width: 1000px) {
    .companies svg {
        width: 150px;
    }
}